.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.background-container {
	display: flex;
	background-color: #30093a;
	height: 80vh;
	justify-content: space-evenly;
	padding: 3rem 3rem 1rem 3rem;
}

.left-container {
	display: flex;
	flex-direction: column;
}

.welcome {
	font-family: 'Satisfy', cursive;
	color: #fff;
	font-size: 0.5em;
	margin: 0;
}

.title {
	font-family: 'Raleway', sans-serif;
	color: #ffffff;
	font-size: 4em;
}

.phrase {
	font-family: 'Satisfy', cursive;
	margin-top: 1rem;
}

.phrase p {
	font-family: 'Satisfy', cursive;
	margin: 0;
	color: #fff;
	font-size: 1.2em;
	font-weight: 300;
}

.button-get-in {
	font-family: 'Raleway', sans-serif;
	font-weight: 900;
	background-color: #fff;
	border-radius: 15px;
	cursor: pointer;
	color: #30093a;
	font-size: 1.2em;
	width: 11vw;
	height: 7vh;
}

.landing-image img {
	max-height: 75vh;
	box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
		rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
		rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.recipe-title {
    font-family: 'Raleway', sans-serif;
}

.recipe-card {
    box-shadow: 16px 17px 46px 7px rgba(117, 5, 209, 0.19);
    -webkit-box-shadow: 16px 17px 46px 7px rgba(117, 5, 209, 0.19);
    -moz-box-shadow: 16px 17px 46px 7px rgba(117, 5, 209, 0.19);
    font-family: 'Raleway', sans-serif;
    border: 4mm outset #30093a;
    border-radius: 5px;
}
.ul-pagination {
    list-style-type: none;
    margin: 0px;
    background-color: #30093a;
    color: #fff;
    display: flex;
    justify-content: center;
}

.p-pagination {
    padding: 0 30px;
    font-family: 'Raleway', sans-serif;
}
.recipes-external-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
    padding-left: 10px;
    padding-top: 10px;
}

.recipes-external-container recipe {
    text-align: center;
}

.recipe {
    text-align: center;
    text-decoration: none;
}
.details-main-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #30093a;
    color: #fff;
    font-family: 'Raleway', sans-serif;
}

.detail-container {
    display: flex;
    padding: 0 2rem;
}

.detail-title {
    text-align: center;
}

.div-left {
    margin: 0 2rem;
}

.div-left img {
    max-width: 40vw;
    margin-top: 1.5rem;
}

.detail-image {
    box-shadow: rgb(0 0 0 / 25%) 0px 12px 8px, rgb(0 0 0 / 12%) 0px -12px 30px,
        rgb(0 0 0 / 12%) 0px 4px 6px, rgb(0 0 0 / 17%) 0px 12px 13px,
        rgb(0 0 0 / 9%) 0px -3px 5px;

}

.div-right {
    margin: 1.5rem 2rem;

}

.detail-summary {
    background-color: #fff;
    color: #30093a;
    font-weight: bold;
    padding: 1px;
    margin-top: 1px;
    margin-bottom: 1px;
    border-radius: 3px;
}

.detail-summary p {
    margin: 1px;
    box-shadow: rgb(0 0 0 / 25%) 0px 12px 8px, rgb(0 0 0 / 12%) 0px -12px 30px,
        rgb(0 0 0 / 12%) 0px 4px 6px, rgb(0 0 0 / 17%) 0px 12px 13px,
        rgb(0 0 0 / 9%) 0px -3px 5px;
}
.searchbar {
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #30093a;
}

.input-search {
	padding: 0 0.5rem;
	height: 5.5vh;
	width: 15vw;
	border-radius: 10px;
	border-color: #30093a;
	font-family: 'Raleway', sans-serif;
	border-width: 1px;
	font-weight: bold;
	font-size: 13px;
}

.button-search {
	font-family: 'Raleway', sans-serif;
	background-color: #b68fd4;
	border-radius: 10px;
	cursor: pointer;
	color: #ffffff;
	width: 8vw;
	height: 6vh;
	margin-left: 1rem;
	border-width: 1px;
}

.button-search:hover {
	background-color: #612797;
	color: #ebebeb;
}
.navbar {
	background-color: #ffffffff;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	height: 10vh;
	font-weight: bolder;
}

.nav-left {
	font-family: 'Raleway', cursive;
	margin-left: 3rem;
	font-size: 2rem;
}

.nav-left a {
	color: #30093a;
	text-decoration: none;
}

.list-item {
	font-family: 'Raleway', cursive;
	list-style-type: none;
	text-decoration: none;
	padding-right: 10px;
}

.list-item a {
	list-style-type: none;
	text-decoration: none;
}
.form-main-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: #30093a;
	color: #fff;
	min-height: 100vh;
	font-family: 'Raleway', sans-serif;
}

.form-main-div {
	display: flex;
}

.form-left-div {
	display: flex;
	flex-direction: column;
	margin: 0 5rem;
}

.form-rigth-div {
	display: flex;
	flex-direction: column;
	margin: 0 5rem;
}

.form-left-div input {
	padding: 0 0.5rem;
	height: 6vh;
	width: 20vw;
	border-radius: 10px;
	margin-bottom: 1rem;
	font-weight: bolder;

}

.form-left-div textarea {
	padding: 1rem 0.5rem;
	height: 8vh;
	width: 20vw;
	border-radius: 5px;
	margin-bottom: 1rem;
	font-weight: bolder;

}

.form-list-diets {
	display: flex;
	flex-direction: column;
}

.button-create {
	display: flex;
	justify-content: center;
	top: 2rem;
}

.button-create button {
	font-family: 'Raleway', sans-serif;
}
.about-background-container {
	display: flex;
	justify-content: center;
	min-height: 90vh;
}

.about-main-container {
	border-radius: 1rem;
	width: 50vw;
	height: 70vh;
	margin-top: 2.5rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 1rem;
	box-shadow: 0px 0px 20px 5px rgb(0 0 0 / 31%);
}

.about-main-container h1 {
	font-family: 'Raleway', sans-serif;
	margin: 0px;
	color: #30093a;
}

.about-info {
	display: flex;
	align-items: center;
}

.about-info p {
	font-family: 'Raleway', sans-serif;
	text-align: justify;
	color: #30093a;
	font-weight: bolder;
}

.about-info-image {
	width: 15vw;
	margin-left: 1rem;
	border-radius: 5px;
}

.about-contact {
	margin: 0px;
	font-family: 'Raleway', sans-serif;
	color: #30093a
}

.about-contact p {
	margin: 0px;
	font-weight: bolder;
}

.about-contact h2 {
	margin: 0px
}

.about-contact a img {
	width: 4vw;
}

.about-contact-icons {
	text-align: center;
}
.order-container {
    padding-top: 10px;
    padding-left: 150px;
    font-family: 'Raleway', sans-serif;
    background-color: #30093a;
    color:#fff;
}

.order-option {
    padding: 0 0.5rem;
    height: 5.5vh;
    width: 15vw;
    border-radius: 10px;
    border-color: #30093a;
    font-family: 'Raleway', sans-serif;
    border-width: 1px;
    font-weight: bold;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

